@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

body {
  font-family: 'Montserrat', Arial, sans-serif;
  background: linear-gradient(135deg, #f8fafc 0%, #e0e7ff 100%);
  margin: 0;
  min-height: 100vh;
}

h1 {
  color: #3b82f6;
  text-align: center;
  margin-bottom: 16px;
  font-weight: 700;
  letter-spacing: 1px;
}

.navbar {
  position: sticky;
  top: 0;
  background: #fff;
  padding: 16px 0;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  z-index: 10;
}

nav {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-bottom: 16px;
}

nav a {
  color: #6366f1;
  text-decoration: none;
  font-weight: 600;
  padding: 8px 20px;
  border-radius: 6px;
  transition: background 0.2s, color 0.2s;
}
nav a:hover {
  background: #6366f1;
  color: #fff;
}

main.container {
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 4px 24px 0 rgba(59, 130, 246, 0.08);
  padding: 32px;
  margin: 24px auto;
  max-width: 800px;
}

table {
  width: 100%;
  border-collapse: collapse;
  background: #f1f5f9;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 2px 8px 0 rgba(59, 130, 246, 0.08);
}
th, td {
  padding: 14px 10px;
  text-align: left;
}
th {
  background: #6366f1;
  color: #fff;
  font-weight: 600;
}
tr:nth-child(even) {
  background: #e0e7ff;
}
tr:hover {
  background: #c7d2fe;
}

button {
  background: #3b82f6;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 8px 16px;
  margin-right: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}
button:hover {
  background: #6366f1;
}
button[style*='color: red'] {
  background: #ef4444;
  color: #fff;
}
button[style*='color: red']:hover {
  background: #b91c1c;
}

form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  background: #f1f5f9;
  padding: 24px;
  border-radius: 12px;
  box-shadow: 0 2px 8px 0 rgba(59, 130, 246, 0.08);
}
input, select {
  padding: 10px;
  border: 1px solid #cbd5e1;
  border-radius: 6px;
  font-size: 1rem;
  background: #fff;
  transition: border 0.2s;
}
input:focus, select:focus {
  border: 1.5px solid #6366f1;
  outline: none;
}

@media (max-width: 600px) {
  .container {
    padding: 12px;
  }
  table, th, td {
    font-size: 0.95rem;
  }
  form {
    padding: 10px;
  }
}
